import { Vec3 } from 'cc';
import { tween } from 'cc';
import { Tween } from 'cc';
import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { IIncresaseData, SlotDragonUtils } from '../base/SlotDragonUtils';
import { Sprite } from 'cc';
import { easing } from 'cc';
import { UITransform } from 'cc';
import { randomRangeInt } from 'cc';
import { sp } from 'cc';
import { Prefab } from 'cc';
import { ParticleSystem } from 'cc';
import { ExtendButton } from 'db://assets/core_tgx/base/ExtendButton';
import { BaseSlot } from 'db://assets/module_basic/ui_slot/BaseSlot';
const { ccclass, property } = _decorator;

@ccclass('SlotDragonGameVariable')
export class SlotDragonGameVariable {
    @property({ type: ExtendButton, displayName: '开始按钮' })
    btnStart: ExtendButton = null;
    @property({ type: ExtendButton, displayName: '加速按钮' })
    btnTurbo: ExtendButton = null;
    @property({ type: ExtendButton, displayName: '减按钮' })
    btnSub: ExtendButton = null;
    @property({ type: ExtendButton, displayName: '加按钮' })
    btnAdd: ExtendButton = null;
    @property({ type: ExtendButton, displayName: '自动按钮' })
    btnAuto: ExtendButton = null;
    @property({ type: ExtendButton, displayName: '停止自动按钮' })
    btnStop: ExtendButton = null;
    @property({ type: ExtendButton, displayName: '下注按钮' })
    btnBet: ExtendButton = null;
    @property(Label)
    autoText: Label = null;

    @property({ type: Label, displayName: '积分' })
    scoreText: Label = null;
    @property({ type: Label, displayName: '下注额' })
    betText: Label = null;
    @property({ type: Label, displayName: '赢的分数' })
    winText: Label = null;
    @property({ type: Label, tooltip: '加速按钮提示' })
    turboTip: Label = null;
    @property({ type: Node, tooltip: '放置所有二级界面' })
    panelParentNode: Node = null;

    @property(BaseSlot)
    scroll: BaseSlot = null;
    @property(Node)
    otherNode: Node = null;
    @property([Node])
    entryMiniAniNode: Node[] = [];
    @property(Node)
    mainBtn: Node = null;
    @property(Node)
    startRefresh: Node = null; //开始按钮旋转处
    @property(Sprite)
    turntableBg: Sprite = null;
    @property(Node)
    laserNode: Node = null;
    @property(Node)
    laserCenterNode: Node = null;
    @property(Label)
    laserMultiple: Label = null;
    @property(Node)
    decorateNode: Node = null;
    @property(UITransform)
    bottomBg: UITransform = null;
    @property(sp.Skeleton)
    ball: sp.Skeleton = null;

    //=======特效==========
    @property(Node)
    effectBlack: Node = null;
    @property(Node)
    turboEffect: Node = null;
    @property(Node)
    btnStartEffect: Node = null;
    @property(Node)
    dragonBallEffect: Node = null;
    @property(Node)
    ballEffectBg: Node = null;
    @property(Node)
    ballBoomEffect: Node = null;
    @property(Node)
    effectParent: Node = null;
    @property(ParticleSystem)
    effectStart: ParticleSystem = null;

    //======预制体=========
    @property(Prefab)
    tailPrefab: Prefab = null;

    private _btnStartTw: Tween<Node> = null;
    public winTw: Tween<any> = null;
    public scoreTw: Tween<any> = null;

    public setStartAni(delayTime) {
        this._btnStartTw && (this._btnStartTw.stop(), this._btnStartTw = null);
        this._btnStartTw = tween(this.startRefresh).
            by(delayTime, { eulerAngles: new Vec3(0, 0, 360) })
            .repeatForever();
        this._btnStartTw.start();
    }

    public updateScore(startNum: number, endNum: number) {
        const param: IIncresaseData = {
            timer: 1
        }
        this.scoreTw?.removeSelf();
        this.scoreTw = SlotDragonUtils.instance.IncreaseNumber(this.scoreText, startNum, endNum, param);
        this.scoreTw.start();
    }

    public updateWin(winScore: number) {
        const param: IIncresaseData = {
            timer: 0.5,
            tag: 3
        }
        this.winTw?.removeSelf();
        this.winTw = SlotDragonUtils.instance.IncreaseNumber(this.winText, 0, winScore, param);
        this.winTw.start();
    }

    resetLaser() {
        this.laserNode.active = true;
        const laserChild = this.laserNode.children;
        for (let i = 0; i < laserChild.length; i++) {
            laserChild[i].position = new Vec3(-85 + i * 160, 0, 0);
        }
    }

    laserScroll(multi: number[]) {
        const laserChild = this.laserNode.children;

        for (let i = 0; i < laserChild.length; i++) {
            laserChild[i].position = new Vec3(-85 + i * 160, 0, 0);
        }

        for (let i = 0; i < laserChild.length; i++) {
            tween(laserChild[i]).by(.2, { position: new Vec3(-160, 0, 0) }).call((target: Node) => {
                if (target.position.x < -244) {
                    target.position = new Vec3(235, 0, 0);
                    target.setSiblingIndex(10);
                    target.getComponent(Label).string = 'x' + multi[randomRangeInt(0, multi.length)];
                }
            }).union().repeatForever().tag(1241).start();

            tween(laserChild[i]).to(.1, { scale: new Vec3(1.3, 1.3, 1.3) })
                .to(.1, { scale: Vec3.ONE })
                .union().repeatForever().tag(1241).start();
        }
    }

    laserRScroll(multi: number[]) {
        const laserChild = this.laserNode.children;
        for (let i = 0; i < laserChild.length; i++) {
            tween(laserChild[i]).by(.5, { position: new Vec3(-450, 0, 0) }).call((target: Node) => {
                if (target.position.x > 234) {
                    target.position = new Vec3(-245, 0, 0);
                    target.setSiblingIndex(0);
                    target.getComponent(Label).string = 'x' + multi[randomRangeInt(0, multi.length)];
                }
            }).union().repeatForever().tag(1241).start();

            tween(laserChild[i]).to(.25, { scale: new Vec3(1.3, 1.3, 1.3) })
                .to(.25, { scale: Vec3.ONE })
                .union().repeatForever().tag(1241).start();
        }
    }

    resetBigLaser(multi: number[]) {
        const laserChild = this.laserCenterNode.children;

        for (let i = 0; i < laserChild.length; i++) {
            const pos = new Vec3(-240 + i * 450, 0, 0);
            laserChild[i].active = true;
            laserChild[i].position = pos;
            laserChild[i].getComponent(Label).string = "x" + multi[i];
            laserChild[i].name = "" + multi[i];
        }

        this.laserCenterNode.active = false;
    }

    laserBigScroll(multi: number[], runTimer: number = .3) {
        const laserChild = this.laserCenterNode.children;
        for (let i = 0; i < laserChild.length; i++) {
            tween(laserChild[i]).by(runTimer, { position: new Vec3(-450, 0, 0) }).call((target: Node) => {
                if (target.position.x <= -689) {
                    target.position = new Vec3(660, 0, 0);
                    target.setSiblingIndex(10);
                    target.getComponent(Label).string = 'x' + multi[randomRangeInt(0, multi.length)];
                }
            }).union().repeatForever().tag(1241).start();

            tween(laserChild[i]).to(runTimer / 2, { scale: new Vec3(1.3, 1.3, 1.3) })
                .to(runTimer / 2, { scale: Vec3.ONE })
                .union().repeatForever().tag(1241).start();
        }
    }
}


