import { Vec3 } from 'cc';
import { Widget } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotDragonMini } from '../../data/SlotDragonGameData';
import { UIDGMiniGame } from './UIDGMiniGame';
import { SlotDragonGameManager } from '../SlotDragonGameManager';
import { SlotDragonAnimation } from '../SlotDragonAnimation';
import { Sprite } from 'cc';
import { SpriteFrame } from 'cc';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
const { ccclass, property } = _decorator;

@ccclass('UIDGMiniGameTip')
export class UIDGMiniGameTip extends Component {
    @property(Node)
    btnFreeSure: Node = null;
    @property(Node)
    btnMiniSure: Node = null;
    @property(Sprite)
    tipSpr: Sprite = null;
    @property([SpriteFrame])
    tipFrames: SpriteFrame[] = [];
    @property(Sprite)
    bgSpr: Sprite = null;
    @property([SpriteFrame])
    bgFrames: SpriteFrame[] = [];
    @property(Node)
    miniDragon: Node = null;
    @property(Node)
    freeDragon: Node = null;

    private static _instance: UIDGMiniGameTip = null;
    public static get instance() {
        return this._instance;
    }

    private _data: SlotDragonMini = null;
    private _callback: Function = null;

    protected onLoad() {
        if (UIDGMiniGameTip._instance) {
            this.node.destroy();
            return;
        }

        UIDGMiniGameTip._instance = this;
        const widget = this.node.getComponent(Widget);
        widget.left = 0;
        widget.right = 0;
        this.node.position = Vec3.ZERO;
        this.node.active = false;
    }

    protected onDestroy(): void {
        UIDGMiniGameTip._instance = null;
    }

    showFreeTip(callback: Function) {
        SlotDragonGameManager.instance.stopAllAudio();
        AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_hoodle_window_appear", 1, ModuleDef.SLOTS_LOONG);
        SlotDragonGameManager.instance.isDialog = true;
        this._callback = callback;
        this.tipSpr.spriteFrame = this.tipFrames[1];
        this.bgSpr.spriteFrame = this.bgFrames[1];
        this.btnFreeSure.active = true;
        this.btnMiniSure.active = false;
        this.miniDragon.active = false;
        this.freeDragon.active = true;
        this.node.active = true;
        if (SlotDragonGameManager.instance.isAuto) {
            this.scheduleOnce(() => {
                this.onBtnSureFree();
            }, 2);
        }
    }

    public show(data: SlotDragonMini) {
        SlotDragonGameManager.instance.stopAllAudio();
        SlotDragonGameManager.instance.isDialog = true;
        AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_hoodle_window_appear", 1, ModuleDef.SLOTS_LOONG);
        this.tipSpr.spriteFrame = this.tipFrames[0];
        this.bgSpr.spriteFrame = this.bgFrames[0];
        this._data = data;
        this.btnFreeSure.active = false;
        this.btnMiniSure.active = true;
        this.miniDragon.active = true;
        this.freeDragon.active = false;
        this.node.active = true;
        if (SlotDragonGameManager.instance.isAuto) {
            this.scheduleOnce(() => {
                this.onBtnSure();
            }, 2);
        }
    }

    private onBtnSureFree() {
        this._callback && this._callback();
        this.unscheduleAllCallbacks();
        SlotDragonGameManager.instance.isDialog = false;
        this.node.active = false;
    }

    private onBtnSure() {
        UIDGMiniGame.instance.initMiniGame(this._data);
        SlotDragonAnimation.instance.isShow(false);
        this.unscheduleAllCallbacks();
        SlotDragonGameManager.instance.isDialog = false;
        this.node.active = false;
    }
}


