import { Widget } from 'cc';
import { JsonAsset } from 'cc';
import { Vec3 } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotDragonMini, SlotDragonMiniData } from '../../data/SlotDragonGameData';
import { tween } from 'cc';
import { easing } from 'cc';
import { SlotDragonGameManager } from '../SlotDragonGameManager';
import { Button } from 'cc';
import { UIDragonResult } from './UIDragonResult';
import { ParticleSystem } from 'cc';
import { Label } from 'cc';
import { Color } from 'cc';
import { randomRangeInt } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

const floorIndex = {
    150: 0,
    10: 1,
    5: 2,
    3: 3,
    20: 4,
    50: 5
}

const collectAudio = ['001', '002', '003'];

@ccclass('UIDGMiniGame')
export class UIDGMiniGame extends Component {
    @property(Node)
    ball: Node = null;
    @property(Node)
    springNode: Node = null;
    @property([Node])
    nailList: Node[] = [];
    @property([Node])
    fanList: Node[] = [];
    @property(Button)
    btnFire: Button = null;
    @property([Node])
    floorList: Node[] = [];

    private static _instance: UIDGMiniGame = null;
    public static get instance() {
        return this._instance;
    }

    private _data: SlotDragonMini = null;
    private _isEnd = false;
    private _isFirst = true;
    private _lastColloctAudio1 = { value: "" };
    private _lastColloctAudio2 = { value: "" };
    private _lastColloctAudio3 = { value: "" };
    private _lastColloctAudio4 = { value: "" };

    private _isPlayAudio1 = false;
    private _isPlayAudio2 = false;
    private _isPlayAudio3 = false;
    private _isPlayAudio4 = false;

    protected onLoad() {
        if (UIDGMiniGame._instance) {
            this.node.destroy();
            return;
        }

        UIDGMiniGame._instance = this;
        const widget = this.node.getComponent(Widget);
        widget.left = 0;
        widget.right = 0;
        this.node.position = Vec3.ZERO;
        this.node.active = false;
    }

    protected onDestroy(): void {
        UIDGMiniGame._instance = null;
    }

    private _jsonAsset: SlotDragonMiniData = null;
    private _startRun = false;
    private _index = 0;

    public initMiniGame(data: SlotDragonMini) {
        AudioMgr.inst.play("audio/AC_sound_8004_music_interface_002", 1, ModuleDef.SLOTS_LOONG);
        this.node.active = true;
        this._isEnd = false;
        this._data = data;
        this._jsonAsset = JSON.parse(data.route);
        this.btnFire.interactable = true;
        this.btnFire.node.active = false;

        this._isPlayAudio1 = false;
        this._isPlayAudio2 = false;
        this._isPlayAudio3 = false;
        this._isPlayAudio4 = false;

        this.scheduleOnce(() => {
            this.onBtnRun();
        }, 1);
    }

    private onBtnRun() {
        this.startRun();
        this.btnFire.interactable = false;
    }

    private startRun() {
        tween(this.springNode).to(0.1, { scale: new Vec3(1, 0.6, 1) })
            .delay(0.1)
            .to(1, { scale: Vec3.ONE }, { easing: easing.elasticOut })
            .start();

        tween(this.ball).to(0.1, { position: new Vec3(395, -387) })
            .delay(0.1)
            .to(0.05, { position: new Vec3(395, -328) })
            .call(() => {
                AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_hoodle_catapult", 1, ModuleDef.SLOTS_LOONG);
                this.run();
            })
            .start();
    }

    public run() {
        // console.log("开始运行", this.jsonAsset.json.data);
        this._index = 0;
        this._startRun = true;
    }

    update(deltaTime: number) {
        if (!this._startRun) return;
        //模拟每帧弹珠的位置，旋转
        this.ball.position = this._jsonAsset.data[this._index].p;
        this.ball.rotation = this._jsonAsset.data[this._index].r;
        for (let i = 0; i < this.fanList.length; i++) {
            this.fanList[i].rotation = this._jsonAsset.fanList[this._index];
        }

        switch (this._jsonAsset.data[this._index].c) {
            case 2:
                if (!this._isPlayAudio4) {
                    this.getAudioName(this._lastColloctAudio4, "AC_sound_8004_dragon_hoodle_crash_springback_004_").then(audioName => {
                        this._isPlayAudio4 = true;
                        this.scheduleOnce(() => {
                            this._isPlayAudio4 = false;
                        }, 0.2);
                    }).catch(err => { });
                }
                for (let i = 0; i < this.nailList.length; i++) {
                    const nail = this.nailList[i].position.clone();
                    const pos = this.ball.position.clone();
                    const isCir = this.isPointInCircle(pos, nail, 90);
                    if (isCir) {
                        tween(this.nailList[i]).to(.1, { scale: new Vec3(1.2, 1.2, 1.2) })
                            .delay(.1)
                            .to(.5, { scale: Vec3.ONE }, { easing: easing.elasticOut })
                            .start();
                        break;
                    }
                }
                break;
            case 2 << 3:
                if (!this._isEnd) {
                    AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_hoodle_score_light", 1, ModuleDef.SLOTS_LOONG);
                    this._isEnd = true;
                    const floorNode = this.floorList[floorIndex[this._data.multi]];
                    floorNode.active = true;
                    floorNode.parent.getComponent(Label).color = Color.YELLOW;
                    floorNode.children.forEach(item => {
                        const pati = item.getComponent(ParticleSystem);
                        pati.stop();
                        pati.play();
                    });
                }
                break;
            case 2 << 5:
                if (!this._isPlayAudio1) {
                    this.getAudioName(this._lastColloctAudio1, "AC_sound_8004_dragon_hoodle_crash_001_").then(audioName => {
                        this._isPlayAudio1 = true;
                        this.scheduleOnce(() => {
                            this._isPlayAudio1 = false;
                        }, .2);
                    }).catch(err => { });
                }
                break;
            case 2 << 6:
                if (!this._isPlayAudio2) {
                    this.getAudioName(this._lastColloctAudio2, "AC_sound_8004_dragon_hoodle_crash_002_").then(audioName => {
                        this._isPlayAudio2 = true;
                        this.scheduleOnce(() => {
                            this._isPlayAudio2 = false;
                        }, 0.2);
                    }).catch(err => { });
                }
                break;
            case 2 << 7:
                if (!this._isPlayAudio3) {
                    this.getAudioName(this._lastColloctAudio3, "AC_sound_8004_dragon_hoodle_crash_fan blade_003_").then(audioName => {
                        this._isPlayAudio3 = true;
                        this.scheduleOnce(() => {
                            this._isPlayAudio3 = false;
                        }, 0.2);
                    }).catch(err => { });
                }
                break;
            case 2 << 8:
                break;
        }
        this._index++;
        if (this._index === this._jsonAsset.data.length) {
            this._startRun = false;
            console.log("结束");
            this.scheduleOnce(() => {
                UIDragonResult.instance.showMiniResult(this._data.winScore);
            }, 1);
        }
    }

    /**
     * 获取跟上次播放不同的音效
     * @param lastAudio 
     * @param prefix 
     * @returns 
     */
    private getAudioName(lastAudio: { value: string }, prefix: string) {
        return new Promise<string>((resolve, reject) => {
            let index = randomRangeInt(0, collectAudio.length);
            let addIndex = 0;
            while (lastAudio.value === collectAudio[index]) {
                index = randomRangeInt(0, collectAudio.length);
                addIndex++;
                //防止半死循环
                if (addIndex >= 5) {
                    lastAudio.value = collectAudio[index];
                    AudioMgr.inst.playOneShot("audio/" + prefix + collectAudio[index], 1, ModuleDef.SLOTS_LOONG);
                    return reject(collectAudio[index]);
                }
            }
            lastAudio.value = collectAudio[index];
            const audioName = prefix + collectAudio[index];
            AudioMgr.inst.playOneShot("audio/" + audioName, 1, ModuleDef.SLOTS_LOONG);
            return resolve(audioName);
        });
    }

    /**
     * 判断点是否在圆内
     * @param targetPoint 目标位置
     * @param centerPoint 中心位置
     * @param ridus 半径
     * @returns 
     */
    public isPointInCircle(targetPoint: Vec3, centerPoint: Vec3, radius: number) {
        const C = new Vec3();
        Vec3.subtract(C, targetPoint, centerPoint);
        const distance = C.length();
        if (distance > radius) return false;
        return true;
    }

    public hide() {
        for (let i = 0; i < this.floorList.length; i++) {
            this.floorList[i].active = false;
            this.floorList[i].parent.getComponent(Label).color = Color.WHITE;
        }
        this._startRun = false;
        this.ball.position = new Vec3(395, -328);
        this.node.active = false;

        if (this._isFirst) {
            this._isFirst = false;
            return;
        }
    }
}


