import { Label } from 'cc';
import { sp } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { UIDragonResult } from './UIDragonResult';
import { UIOpacity } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { SlotDragonUtils } from '../../base/SlotDragonUtils';
import { SlotDragonGameManager } from '../SlotDragonGameManager';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('UIDragonResultItem')
export class UIDragonResultItem extends Component {
    @property(Label)
    winLab: Label = null;
    @property(Node)
    effectNode: Node = null;
    @property
    panelLevel: number = -1; //-1最大的页面等级

    private _tw = null;
    private _betScore = 0;
    private _winScore = 0;
    private _collectId = -1; //collect 音效id

    public showMini(winScore: number) {
        this._winScore = winScore;
        this.node.active = true;
        this.winLab.string = "0.00";
        tween(this.node.getComponent(UIOpacity)).to(0.3, { opacity: 255 }).call(() => {
            const cb = () => {
                this.stopCollectAudio();
                if (SlotDragonGameManager.instance.isAuto) {
                    this.scheduleOnce(() => {
                        UIDragonResult.instance.onBtnBack();
                    }, 1);
                } else {
                    UIDragonResult.instance.btnClose.active = true;
                    UIDragonResult.instance.offListener();
                }
            }
            this._tw = SlotDragonUtils.instance.IncreaseNumber(this.winLab, 0, winScore, { timer: 2, callback: cb });
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_bigwin_003", 1, ModuleDef.SLOTS_LOONG);
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_number_collect", 1, ModuleDef.SLOTS_LOONG).then(id => this._collectId = id);
            this._tw.start();
        }).start();
        tween(this.node).to(0.3, { scale: Vec3.ONE }).start();
    }

    public showFree(winScore: number) {
        this._winScore = winScore;
        this.node.active = true;
        this.winLab.string = "0.00";
        tween(this.node.getComponent(UIOpacity)).to(0.3, { opacity: 255 }).call(() => {
            const cb = () => {
                this.stopCollectAudio();
                if (SlotDragonGameManager.instance.isAuto) {
                    this.scheduleOnce(() => {
                        UIDragonResult.instance.onBtnClose();
                    }, 1);
                } else {
                    UIDragonResult.instance.btnFreeClose.active = true;
                    UIDragonResult.instance.offListener();
                }
            }
            this._tw = SlotDragonUtils.instance.IncreaseNumber(this.winLab, 0, winScore, { timer: 2, callback: cb });
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_bigwin_003", 1, ModuleDef.SLOTS_LOONG);
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_number_collect", 1, ModuleDef.SLOTS_LOONG).then(id => this._collectId = id);
            this._tw.start();
        }).start();
        tween(this.node).to(0.3, { scale: Vec3.ONE }).start();
    }

    public show(betScore: number, winScore: number, startBet: number) {
        this._betScore = betScore;
        this._winScore = winScore;
        this.node.active = true;
        let maxBet = 0;
        let level = 0;
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            level = 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === -1) {
            level = 100;
            maxBet = winScore;
        }
        this.winLab.string = Num.exchangeToThousands(1, startBet);
        if (this.panelLevel === 20) {
            startBet = 0;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            startBet = betScore * 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else {
            startBet = betScore * 100;
            maxBet = winScore;
        }
        const cb = () => {
            this.stopCollectAudio();
            if (winScore > maxBet) {
                UIDragonResult.instance.showNext(this.panelLevel, betScore, winScore);
            } else {
                UIDragonResult.instance.isBest = true;
                this.scheduleOnce(() => {
                    UIDragonResult.instance.onBtnClose();
                }, 2);
            }
            UIDragonResult.instance.isNext = true;
        }

        this.node.getComponent(UIOpacity).opacity = 0;
        this.node.scale = new Vec3(1, 0, 1);
        tween(this.node.getComponent(UIOpacity)).to(0.3, { opacity: 255 }).call(() => {
            this._tw = SlotDragonUtils.instance.IncreaseNumber(this.winLab, startBet, maxBet, { timer: 2.6, callback: cb.bind(this) });
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_bigwin_001", 1, ModuleDef.SLOTS_LOONG);
            this._tw.start();
        }).start();
        tween(this.node).to(0.3, { scale: Vec3.ONE }).start();
    }

    public show2(betScore: number, winScore: number, startBet: number) {
        this._betScore = betScore;
        this._winScore = winScore;
        this.node.active = true;
        let maxBet = 0;
        let level = 0;
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_bigwin_002", 1, ModuleDef.SLOTS_LOONG);
            level = 20;
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === -1) {
            AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_bigwin_003", 1, ModuleDef.SLOTS_LOONG);
            level = 100;
            maxBet = winScore;
        }

        this.winLab.string = startBet + '';
        if (this.panelLevel === 20) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else if (this.panelLevel === 100) {
            maxBet = betScore * this.panelLevel;
            maxBet = winScore >= maxBet ? maxBet : winScore;
        } else {
            maxBet = winScore;
        }
        const cb = () => {
            this.stopCollectAudio();
            if (winScore > maxBet) {
                UIDragonResult.instance.showNext(this.panelLevel, betScore, winScore);
            } else {
                UIDragonResult.instance.isBest = true;
                this.scheduleOnce(() => {
                    UIDragonResult.instance.onBtnClose();
                }, 2);
            }
            UIDragonResult.instance.isNext = true;

        }

        this.effectNode.getComponent(UIOpacity).opacity = 0;
        this.effectNode.scale = new Vec3(1, 0, 1);
        tween(this.effectNode.getComponent(UIOpacity)).to(0.3, { opacity: 255 }).start();
        tween(this.effectNode).to(0.3, { scale: Vec3.ONE }).start();
        AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_number_collect", 1, ModuleDef.SLOTS_LOONG).then(id => this._collectId = id);
        this._tw = SlotDragonUtils.instance.IncreaseNumber(this.winLab, startBet, maxBet, { timer: 2.1, callback: cb.bind(this) });
        this._tw.start();
    }

    public setWinScore(winScore: number) {
        this.node.active = true;
        this.winLab.string = winScore + '';
    }

    public setWinMiniScore() {
        this._tw && (this._tw.stop(), this._tw = null, this.stopCollectAudio());
        this.winLab.string = this._winScore + '';
    }

    public unAllCallbacks() {
        this.unscheduleAllCallbacks();
        let maxBet = this._betScore * this.panelLevel;
        maxBet = this._winScore >= maxBet ? maxBet : this._winScore;
        if (this.panelLevel === -1) maxBet = this._winScore;
        this.winLab.string = maxBet + '';
        if (this._winScore <= maxBet) {
            this.scheduleOnce(() => {
                UIDragonResult.instance.onBtnClose();
            }, 2);
        }
        this._tw && (this._tw.stop(), this._tw = null, this.stopCollectAudio());
    }

    public hide() {
        this.unscheduleAllCallbacks();
        this._tw && (this._tw.stop(), this._tw = null, this.stopCollectAudio());
        this.node.active = false;
    }

    private stopCollectAudio() {
        if (this._collectId !== -1) {
            AudioMgr.inst.stopOneShot(this._collectId, "AC_sound_8004_dragon_number_collect", ModuleDef.SLOTS_LOONG);
            this._collectId = -1;
        }
    }
}


