import { Vec3 } from 'cc';
import { Quat } from 'cc';
import { Enum } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

const MessgeDefine = {
    Request: {
        start: "startgame",
        selected: "selected",
        route: "getminiroute",
    },
    Response: {
        GameScene: "slot_dragon_game_scene",
    }
}

const EventName = {
    "clickGameItem": "clickGameItem",
}

const Auto = {
    AutoIndex: [10, 30, 50, 100, 1000],
}

/**
 * 缓存的名字
 * @param betScore 下注额
 */
const cacheName = {
    betScore: '_betScore',
}

export enum DragonGameStatus {
    start, //游戏开始
    over,//游戏结束
    mini, //进入小游戏
}
Enum(DragonGameStatus);

@ccclass('SlotDragonGameData')
export class SlotDragonGameData extends Component {
    public static MessgeDefine = MessgeDefine;
    public static EventName = EventName;
    public static Auto = Auto;
    public static cacheName = cacheName;

    public static autoCountIndex = 0;
    public static leftAutoCount = 0;
    public static reduceLabel = 0;
    public static addLabel = 0;
    public static winLabel = 0;
}

/**
 * 开始游戏返回数据格式
 */
export interface SlotDragonStartGameReq {
    betScore: number, //下注额
    winScore: number, //中奖金额
    code: number, //返回码
    main: SlotDragonBody, //主游戏数据
    mini: SlotDragonMini, //小游戏数据
    free: SlotDragonFree, //免费游戏数据
}

export interface SlotDragonFree {
    multi: number,
    winScore: number,
}

/**
 * 主游戏格式
 */
export interface SlotDragonBody {
    colums: SlotDragonColums[], //每个位置的元素
    lines: SlotDragonLine[], //中奖线
    winScore: number, //中奖金额
    hitType: number, //中奖类型 0-未中奖 1-线路中奖 2-积分中奖
    isAllSame: number, //是否单元素
    multi: number[], //翻倍值[2,5,10]
}

/**
 * 元素格式
 */
export interface SlotDragonColums {
    elementId: number[],
}

export interface SlotDragonElement {
    elementId: number, //301为积分牌
}

export interface SlotDragonMini {
    winScore: number,
    multi: number,
    route: string,
    routeId: number,
    routeLength: number,
    finished: number,
}

/**
 * 小游戏返回格式
 */
export interface SlotDragonMiniRouteResp {
    code: number, //返回码
    route: string, //小游戏路线json片段
    routeId: number, //json Id根据这个id去获取json片段
    routeLength: number, //json长度
    finished: number, //是否发完全部json
}

export interface ILineData {
    lineWheel: number[];
}

/**
 * 中奖线格式
 */
export interface SlotDragonLine {
    line: number[], //中奖线的位置
    lineNo: number, //中奖线的编号
    lineScore: number //当前线路的中奖金额
}

/**
 * 小游戏的json格式
 */
export interface SlotDragonMiniData {
    data: IDragonMiniData[], //弹珠的运动的所有位置及旋转角度
    fanList: Quat[], //每个风扇的旋转角度
    multiple: number, //倍数/中奖位置
}

/**
 * 弹珠的运动的格式
 */
export interface IDragonMiniData {
    p: Vec3, //弹珠每帧的位置
    r: Quat, //弹珠每帧的旋转角度
    c: number //碰撞到的物体group
}


