import { Label } from 'cc';
import { Font } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotDragonBody, SlotDragonLine } from '../data/SlotDragonGameData';
import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { SlotDragonGameManager } from './SlotDragonGameManager';
import { SlotDragonGameElement } from './SlotDragonGameElement';
import { macro } from 'cc';
import { find } from 'cc';
import { SlotDragonAnimation } from './SlotDragonAnimation';
import { Color } from 'cc';
import { ISlotRollInfo } from 'db://assets/module_basic/ui_slot/BaseSlot';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

const column = [4, 4, 4];
const posInterval = [230, 230, 230];
const lineScorePos = [new Vec3(0, 712), new Vec3(0, 477), new Vec3(0, 246), new Vec3(0, 26)];
//最终奖品管理
@ccclass('SlotDragonPrizeManager')
export class SlotDragonPrizeManager extends Component {
    @property(Node)
    maskNode: Node = null;
    @property(Node)
    letfLightNode: Node = null;
    @property(Node)
    rightLightNode: Node = null;
    @property(Node)
    lineNode: Node = null;
    @property(Node)
    endContent: Node = null;
    @property(Node)
    prizeContent: Node = null;
    @property(Prefab)
    elementItem: Prefab = null;
    @property(Node)
    lineScoreNode: Node = null;
    @property(Label)
    lineScoreText: Label = null;
    @property([Font])
    lineFont: Font[] = [];

    private static _instance: SlotDragonPrizeManager = null;
    public static get instance() {
        return this._instance;
    }

    private _lineIndex = 0; //线路中奖时轮播下标
    private _lines: SlotDragonLine[] = []; //所有中奖的线
    private _scoreElements: Node[] = [];

    protected onLoad(): void {
        if (SlotDragonPrizeManager._instance) {
            this.node.destroy();
            return;
        }

        SlotDragonPrizeManager._instance = this;
        this.gameSetup();
    }

    protected onDestroy(): void {
        SlotDragonPrizeManager._instance = null;
    }

    private gameSetup() {
        for (let i = 0; i < this.endContent.children.length; i++) {
            for (let j = 0; j < column[i]; j++) {
                const element = instantiate(this.elementItem);
                element.parent = this.endContent.children[i];
                element.position = new Vec3(0, -112 - posInterval[i] * j);
                element.name = j.toString();
            }

            for (let j = 0; j < column[i]; j++) {
                const element = instantiate(this.elementItem);
                element.parent = this.prizeContent.children[i];
                element.position = new Vec3(0, -112 + posInterval[i] + posInterval[i] * j);
                element.name = j.toString();
                element.active = false;
            }
        }

        this.endContent.active = false;
        this.prizeContent.active = false;
        this.showPrize(SlotDragonGameManager.instance.getFrameIds(), 0, 0);
    }

    public showPrize(res: ISlotRollInfo[][], prizeType: number, betScore: number, mainRes: SlotDragonBody = null, callback: Function = null) {
        this._lines = mainRes?.lines;
        this.endContent.active = true;
        for (let i = 0; i < res.length; i++) {
            for (let j = 0; j < res[i].length; j++) {
                const element = this.endContent.children[i].children[j].getComponent(SlotDragonGameElement);
                element.initElement(res[i][j].name, res[i][j].text);
                element.node.active = true;
                element.setSpine('', true, betScore);

                const element2 = this.prizeContent.children[i].children[j].getComponent(SlotDragonGameElement);
                element2.initElement(res[i][j].name, res[i][j].text);
                element2.showSpine(res[i][j].name);
            }
        }

        if (prizeType === 1) {
            //线路中奖
            SlotDragonAnimation.instance.playAni(SlotDragonAnimation.instance.DragonAniName.celebrate, false);
            this.linePrize();
            callback && callback();
        } else if (prizeType === 2) {
            //积分中奖
        } else {
            callback && callback();
        }
    }

    //线路中奖
    private linePrize() {
        this.prizeContent.active = true;
        this.closeAllLine();
        this._lineIndex = this._lines.length;
        this.maskNode.active = true;
        this._lines.sort((a, b) => {
            return a.lineNo - b.lineNo;
        })
        this.schedule(this.carouselLine.bind(this), 1.5, macro.REPEAT_FOREVER, .1);
    }

    //轮播中奖线路
    private carouselLine() {
        this.resetPrizeElement();
        this.closeAllLine();
        if (this._lines.length === 1) this._lineIndex = 0;
        this.showEndContent();
        if (this._lineIndex === this._lines.length) {
            //显示所有线路
            if (!SlotDragonGameManager.instance.isDialog) AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_connect_multiple", 1, ModuleDef.SLOTS_LOONG);
            this.lineScoreNode.active = false;
            for (let i = 0; i < this._lines.length; i++) {
                const singleLine = this._lines[i];
                find(singleLine.lineNo.toString(), this.lineNode).active = true;
                const leftLab = find(singleLine.lineNo.toString(), this.letfLightNode).getComponent(Label);
                const rightLab = find(singleLine.lineNo.toString(), this.rightLightNode).getComponent(Label);

                leftLab.color = new Color().fromHEX("#FFEF39");
                rightLab.color = new Color().fromHEX("#FFEF39");
                leftLab.enableOutline = true;
                rightLab.enableOutline = true;
                leftLab.outlineColor = new Color().fromHEX("#743F03");
                rightLab.outlineColor = new Color().fromHEX("#743F03");
                leftLab.outlineWidth = 2;
                rightLab.outlineWidth = 2;

                for (let j = 0; j < singleLine.line.length; j++) {
                    const element = this.prizeContent.children[j].children[singleLine.line[j]];
                    const element2 = this.endContent.children[j].children[singleLine.line[j]];
                    element2.active = false;
                    element.active = true;
                    element.position = new Vec3(0, -112 - posInterval[j] * singleLine.line[j]);
                    element.getComponent(SlotDragonGameElement).prizeEffect();
                    element.getComponent(SlotDragonGameElement).setSpine('1', false);
                }
            }
        } else {
            if (!SlotDragonGameManager.instance.isDialog) AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_connect_single", 1, ModuleDef.SLOTS_LOONG);
            const singleLine = this._lines[this._lineIndex];
            find(singleLine.lineNo.toString(), this.lineNode).active = true;
            const leftLab = find(singleLine.lineNo.toString(), this.letfLightNode).getComponent(Label);
            const rightLab = find(singleLine.lineNo.toString(), this.rightLightNode).getComponent(Label);

            leftLab.color = new Color().fromHEX("#FFEF39");
            rightLab.color = new Color().fromHEX("#FFEF39");
            leftLab.enableOutline = true;
            rightLab.enableOutline = true;
            leftLab.outlineColor = new Color().fromHEX("#743F03");
            rightLab.outlineColor = new Color().fromHEX("#743F03");
            leftLab.outlineWidth = 2;
            rightLab.outlineWidth = 2;

            this.lineScoreNode.active = true;
            this.lineScoreText.string = singleLine.lineScore + '';
            this.lineScoreNode.position = lineScorePos[singleLine.line[1]];
            for (let j = 0; j < singleLine.line.length; j++) {
                const element = this.prizeContent.children[j].children[singleLine.line[j]];
                const element2 = this.endContent.children[j].children[singleLine.line[j]];
                element2.active = false;
                element.active = true;
                element.position = new Vec3(0, -112 - posInterval[j] * singleLine.line[j]);
                element.getComponent(SlotDragonGameElement).prizeEffect();
                element.getComponent(SlotDragonGameElement).setSpine('1', false);
            }
        }

        this._lineIndex++;
        if (this._lineIndex > this._lines.length) this._lineIndex = 0;
    }

    public showElementSpine(info: ISlotRollInfo, index: number, j: number) {
        const element = this.endContent.children[index].children[j].getComponent(SlotDragonGameElement);
        element.node.active = true;
        element.node.position = new Vec3(0, -112 - posInterval[index] * j);
        element.initElement(info.name, info.text, '3');
    }

    public showEndContent(isActive = true) {
        for (let i = 0; i < this.endContent.children.length; i++) {
            for (let j = 0; j < column[i]; j++) {
                const element = this.endContent.children[i].children[j];
                element.getComponent(SlotDragonGameElement).setSpine('', false);
                element.active = isActive;
            }
        }
    }

    //重设中奖元素位置
    private resetPrizeElement() {
        for (let i = 0; i < this.prizeContent.children.length; i++) {
            for (let j = 0; j < column[i]; j++) {
                const element = this.prizeContent.children[i].children[j];
                element.position = new Vec3(0, -112 + posInterval[i] + posInterval[i] * j);
                element.active = false;
            }
        }
    }

    public closeAllLine() {
        for (let i = 0; i < this.lineNode.children.length; i++) {
            this.lineNode.children[i].active = false;
        }

        for (let i = 0; i < this.letfLightNode.children.length; i++) {
            const leftLab = this.letfLightNode.children[i].getComponent(Label);
            const rightLab = this.rightLightNode.children[i].getComponent(Label);
            leftLab.color = new Color().fromHEX("#7B6F5F");
            rightLab.color = new Color().fromHEX("#7B6F5F");
            leftLab.enableOutline = false;
            rightLab.enableOutline = false;
        }
    }

    public hideEndContet() {
        this.resetPrizeElement();
        this.closeAllLine();
        this.lineScoreNode.active = false;
        this.endContent.active = false;
        this.prizeContent.active = false;
        this.maskNode.active = false;
        this.unscheduleAllCallbacks();
    }
}


