import { Tween } from 'cc';
import { UITransform } from 'cc';
import { tween } from 'cc';
import { SpriteFrame } from 'cc';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { RichText } from 'cc';
import { Sprite } from 'cc';
import { macro } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotDragonUtils } from '../base/SlotDragonUtils';
import { ParticleSystem } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;

const NoticeMsg = ['slotDragon_tip1', 'slotDragon_tip2'];
@ccclass('SlotDragonNoticeManager')
export class SlotDragonNoticeManager extends Component {
    @property(Sprite)
    noticeBg: Sprite = null;
    @property(RichText)
    msgLab: RichText = null;
    @property(UITransform)
    maskTrans: UITransform = null;
    @property(Node)
    scoreNode: Node = null;
    @property(Sprite)
    winSpr: Sprite = null;
    @property(Label)
    winScoreLab: Label = null;
    @property([SpriteFrame])
    winFrames: SpriteFrame[] = [];
    @property([SpriteFrame])
    winTextFrames: SpriteFrame[] = [];
    @property([Node])
    bigPrizeEffectList: Node[] = [];
    @property([Node])
    prizeEffectList: Node[] = [];

    private static _instance: SlotDragonNoticeManager = null;
    public static get instance() {
        return this._instance;
    }

    private _noticeIndex = 1;
    private _msgTween: Tween<Node> = null;
    private _miniWinScore = 0;

    protected onLoad(): void {
        if (SlotDragonNoticeManager._instance) {
            this.node.destroy();
            return;
        }

        SlotDragonNoticeManager._instance = this;
        this.showNotice();
    }

    protected onDestroy(): void {
        SlotDragonNoticeManager._instance = null;
    }

    //==============结算分数===================
    public balanceScore(betScore: number, winScore: number, callback: Function = null) {
        if (winScore === 0 || betScore === 0) {
            callback && callback();
            return;
        }
        this.scoreNode.active = true;
        this.msgLab.node.active = false;
        AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_hoodle_score_collect", 1, ModuleDef.SLOTS_LOONG);
        const mult = winScore / betScore;
        if (mult > 10) {
            if (mult <= 100) {
                this.bigPrizeEffectList[1].children.forEach(node => {
                    node.getComponent(ParticleSystem).stop();
                    node.getComponent(ParticleSystem).play();
                });
                this.prizeEffectList[1].children.forEach(node => {
                    node.getComponent(ParticleSystem).stop();
                    node.getComponent(ParticleSystem).play();
                });
                this.noticeBg.spriteFrame = this.winFrames[1];
            } else {
                this.bigPrizeEffectList[2].children.forEach(node => {
                    node.getComponent(ParticleSystem).stop();
                    node.getComponent(ParticleSystem).play();
                });
                this.prizeEffectList[2].children.forEach(node => {
                    node.getComponent(ParticleSystem).stop();
                    node.getComponent(ParticleSystem).play();
                });
                this.noticeBg.spriteFrame = this.winFrames[2];
            }
        } else {
            this.noticeBg.spriteFrame = this.winFrames[0];
            this.bigPrizeEffectList[0].children.forEach(node => {
                node.getComponent(ParticleSystem).stop();
                node.getComponent(ParticleSystem).play();
            });
            this.prizeEffectList[0].children.forEach(node => {
                node.getComponent(ParticleSystem).stop();
                node.getComponent(ParticleSystem).play();
            });
        }

        if (mult >= 5) SlotDragonUtils.instance.IncreaseNumber(this.winScoreLab, 0, winScore, { prefix: 'win ', timer: 1 }).start();
        else this.winScoreLab.string = 'win ' + winScore;

        tween(this.node).to(.2, { scale: new Vec3(1.2, 1.2, 1.2) })
            .delay(.1)
            .to(.2, { scale: Vec3.ONE })
            .call(() => {
                callback && callback();
            })
            .union()
            .start();
    }

    public resetNoticeBg() {
        this.noticeBg.spriteFrame = this.winFrames[0];
    }

    public hideScoreNode() {
        this._miniWinScore = 0;
        this.scoreNode.active = false;
        this.msgLab.node.active = true;
        this.resetNoticeBg();
    }

    //================轮播===================
    public showNotice() {
        this.schedule(this.setNotice, 8, macro.REPEAT_FOREVER, .1);
    }

    private setNotice() {
        this._msgTween && (this._msgTween.stop(), this._msgTween = null);
        this.msgLab.string = LanguageData.getLangByID(NoticeMsg[this._noticeIndex]);
        this.scheduleOnce(() => {
            const size = this.msgLab.getComponent(UITransform).contentSize;
            const maskWidth = this.maskTrans.contentSize.width;
            if (size.width > maskWidth) {
                this.msgLab.node.position = new Vec3((size.width - maskWidth) / 2);
                this.scheduleOnce(() => {
                    tween(this.msgLab.node).removeSelf();
                    this._msgTween = tween(this.msgLab.node).to(7, { position: new Vec3(-size.width, 0) });
                    this._msgTween.start();
                }, 1);

            }
            else this.msgLab.node.position = Vec3.ZERO;
        }, 1 / 60);
        this._noticeIndex++;
        if (this._noticeIndex >= NoticeMsg.length) this._noticeIndex = 0;
    }

    public stopNotice() {
        this._msgTween && (this._msgTween.stop(), this._msgTween = null);
    }
}


