import { sp } from 'cc';
import { macro } from 'cc';
import { Enum } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { SlotDragonGameManager } from './SlotDragonGameManager';
import { Vec3 } from 'cc';
import { UIDGMiniGameTip } from './UI/UIDGMiniGameTip';
const { ccclass, property } = _decorator;

const DragonAniName = {
    idle: 'idle',
    cute: 'cute', //卖萌
    celebrate: 'celebrate', //庆祝
    ball1: 'ball1', //抛球1
    ball2: 'ball2' //抛球2
}

enum DragonAniStatus {
    idle,
    cute,
    celebrate,
    ball1,
    ball2
}
Enum(DragonAniStatus);

@ccclass('SlotDragonAnimation')
export class SlotDragonAnimation extends Component {
    @property([sp.SkeletonData])
    aniData: sp.SkeletonData[] = [];
    @property(Node)
    effect: Node = null;

    private static _instance: SlotDragonAnimation = null;
    public static get instance() {
        return this._instance;
    }

    public DragonAniName = DragonAniName;
    private _status: DragonAniStatus = DragonAniStatus.idle;
    private _animation: sp.Skeleton = null;
    private _isPlayLoop: boolean = true;

    protected onLoad(): void {
        if (SlotDragonAnimation._instance) {
            this.node.destroy();
            return;
        }

        SlotDragonAnimation._instance = this;
        this.gameSetup();
    }

    protected onDestroy(): void {
        SlotDragonAnimation._instance = null;
    }

    private gameSetup() {
        this._animation = this.node.getComponent(sp.Skeleton);
        this._animation.setCompleteListener(ani => {
            if (!this.node.activeInHierarchy || this._isPlayLoop) return;
            if (this._status === DragonAniStatus.ball1) {
                this._isPlayLoop = true;
                this._animation.setAnimation(0, "1", true);
                SlotDragonGameManager.instance.freeMiniGame();
                return;
            }
            if (this._status !== DragonAniStatus.cute) this.openAniDownTime();
            this.playAni(DragonAniName.idle, true);
        });

        this.openAniDownTime();
    }

    public playAni(aniName: string, isLoop: boolean) {
        if (this._status === DragonAniStatus[aniName] || !this.node.activeInHierarchy) return;
        if (aniName !== DragonAniName.idle && aniName !== DragonAniName.cute) {
            this.unschedule(this.playCute);
        }
        if (aniName === DragonAniName.ball1) this.effect.active = false;
        if (aniName === DragonAniName.ball2) this.effect.active = true;
        this._isPlayLoop = isLoop;
        this._status = DragonAniStatus[aniName];
        this._animation.paused = true;
        this.scheduleOnce(() => {
            this._animation.paused = false;
            this._animation.skeletonData = this.aniData[this._status];
            this._animation.setAnimation(0, "animation", isLoop);
        }, 1 / 60);

    }

    public openAniDownTime() {
        this.schedule(this.playCute, 10, macro.REPEAT_FOREVER);
    }

    public resetBallParet(parent: Node) {
        this.node.parent = parent;
        this.node.setSiblingIndex(2);
        this.node.position = new Vec3(54, 932);
    }

    public resetPlay() {
        this.openAniDownTime();
        this.playAni(DragonAniName.ball2, false);
    }

    public isShow(isShow = true) {
        this.node.active = isShow;
    }

    public playCute() {
        this.playAni(DragonAniName.cute, false);
    }
}


