    private successStart(res: SlotDragonStartGameReq) {
        const betScore = res.betScore ? res.betScore : 0;
        this._score -= betScore;
        this._totalLose += betScore;
        this._totalWin -= betScore;
        this.variable.scoreTw && (this.variable.scoreTw.stop());
        this.variable.scoreText.string = Num.exchangeToThousands(1, this._score);
        let elementIds: ISlotRollInfo[][] = [];
        this._isFreeGame = res.free ? true : false;
        if (res.mini) {
            //小游戏
            this._isEntryMiniGame = true;
            // if (!res.mini.finished) {
            //     this.getMiniRoute(res.mini.routeId, res.mini.routeLength);
            // } else {
            elementIds = this.getElementIds(res.main.colums);
            this.variable.scroll.roll(elementIds, () => {
                this.variable.scroll.node.active = false;
                SlotDragonPrizeManager.instance.showPrize(elementIds, 0, res.betScore);

                // SlotDragonPrizeManager.instance.endContent.active = true;
                // SlotDragonPrizeManager.instance.showEndContent(false);

                UIDGMiniGameTip.instance.show(res.mini);
            }, (i) => {

            });
            this.scheduleOnce(() => {
                this.variable.scroll.stop();
            }, 1);
            // }
        } else if (res.main && res.main.colums.length > 0) {
            //正常游戏
            this._isEntryMiniGame = false;
            elementIds = this.getElementIds(res.main.colums);

            SlotDragonPrizeManager.instance.endContent.active = true;
            SlotDragonPrizeManager.instance.showEndContent(false);
            this.variable.scroll.roll(elementIds, () => {
                this.variable.scroll.node.active = false;
                SlotDragonPrizeManager.instance.showPrize(elementIds, res.main.hitType, res.betScore, res.main, this.gameOver.bind(this));
            }, (i) => {

            });

            let delayTime = 0.06;
            const random = Math.random();
            //fakeMiniRate万分比
            if (random < SlotDragonSceneManager.instance.fakeMiniRate / 10000) {
                //假装要进小游戏
                delayTime = 4;
                this.fakeMiniGame();
            }

            this.scheduleOnce(() => {
                if (this.variable.scroll.stopType === SlotStopType.立即停止) {
                    if (this._id1 != -1) (AudioMgr.inst.stopOneShot(this._id1, 'audio/AC_sound_8004_dragon_turntable_glitter', ModuleDef.SLOTS_LOONG), this._id1 = -1);
                    if (!this.isDialog) AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_turntable_stop", 1, ModuleDef.SLOTS_LOONG).then(id => {
                        this._id2 = id;
                    });
                } else {
                    for (let i = 0; i < 3; i++) {
                        let interval = 0;
                        if (i === 1) {
                            interval = 0.4;
                        } else if (i === 2) {
                            interval = 1.02;
                        }
                        this.scheduleOnce(() => {
                            if (!this.isDialog) AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_turntable_stop", 1, ModuleDef.SLOTS_LOONG).then(id => {
                                this._id2 = id;
                            });
                        }, interval);
                    }
                }
            }, delayTime + 0.4);
            this.scheduleOnce(() => {
                if (!this._isSpeed) {
                    this.scheduleOnce(() => {
                        if (this._id1 != -1) (AudioMgr.inst.stopOneShot(this._id1, 'audio/AC_sound_8004_dragon_turntable_glitter', ModuleDef.SLOTS_LOONG), this._id1 = -1);
                    }, 1.05);
                }
                this.variable.scroll.stop();
            }, delayTime);
        }
    }

    private _tailNum = 0;
    public showTail(currentPos: Vec3) {
        if (this._tailNum === 0 && !this.isDialog) AudioMgr.inst.playOneShot("audio/AC_sound_8004_dragon_light_up", 1, ModuleDef.SLOTS_LOONG).then(id => {
            this._id3 = id;
        });
        this._tailNum++;
        const item = instantiate(this.variable.tailPrefab);
        item.parent = this.variable.effectParent;
        item.worldPosition = currentPos;
        const boomPos = this.variable.ballBoomEffect.worldPosition.clone();
        const endPos = new Vec3(boomPos.x, currentPos.y);

        SlotDragonUtils.instance.bezierTo(item, 0.3, 0, currentPos, endPos, boomPos).call((node: Node) => {
            node.removeFromParent();
            this.variable.ballBoomEffect.children.forEach(v => {
                v.getComponent(ParticleSystem).stop();
                v.getComponent(ParticleSystem).play();
            });
        }).start();
    }

