import { _decorator, Component, Node } from 'cc';
import { ILineData, SlotDragonGameData } from '../data/SlotDragonGameData';
import { SlotDragonGameManager } from './SlotDragonGameManager';
import { SlotDragonUtils } from '../base/SlotDragonUtils';
import { view } from 'cc';
import { Vec3 } from 'cc';
import { UITransform } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SlotDragonSceneManager')
export class SlotDragonSceneManager extends Component {
    @property({ type: UITransform, tooltip: "游戏的主节点" })
    gameMainTran: UITransform = null;
    private static _instance: SlotDragonSceneManager = null;
    public static get instance() {
        return this._instance;
    }

    public betScore: number[] = [];
    public elementOdd: number[] = []; //不除100
    public lineCount: number = 0;
    public fakeMiniRate: number = 0; //假装要进小游戏的概率，万分比，/10000
    public pointScores: number[] = [];
    public wheel: ILineData[] = [];
    public wheelData: number[][] = [];
    public miniWheelData: number[][] = [];
    public isOpenVideo: boolean = true;
    public isFirstEnty: boolean = true;
    public multi: number[] = [];
    private _isFirst: boolean = true;

    onLoad() {
        if (SlotDragonSceneManager._instance) {
            this.node.destroy();
            return;
        }

        SlotDragonSceneManager._instance = this;
        this.gameSetup();
    }

    protected onDestroy(): void {
        SlotDragonSceneManager._instance = null;
        SlotDragonUtils.instance = null;
    }

    private gameSetup() {
        //适配
        // const v = view.getVisibleSize();
        // const scaleV = v.width / this.gameMainTran.height;
        // this.gameMainTran.node.scale = new Vec3(scaleV, scaleV, 1);

        // this.events.put(SlotDragonGameData.MessgeDefine.Response.GameScene + store.room.id, this.initGameScene.bind(this));
    }

    startGame() {
        this.initGameScene({
            betScore: [100, 200, 300, 400, 500, 600, 700, 800, 900], elementOdd: { 101: 3, 102: 5, 103: 8, 104: 10, 105: 25, 106: 100, 201: 250 }, lineCount: 10, fakeMiniRate: 10,
            pointScores: [100, 300, 500, 700, 900, 1100], wheel: [{ lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }],
            multi: [2, 5, 10, 20, 30, 50, 100]
        })
    }

    private initGameScene(res) {
        console.log('---龙--', res);
        this.betScore = res.betScore;
        this.elementOdd = res.elementOdd;
        this.lineCount = res.lineCount;
        this.fakeMiniRate = res.fakeMiniRate ? res.fakeMiniRate : 0;
        this.pointScores = res.pointScores;
        this.wheel = res.wheel;
        this.multi = res.multi;
        this.multi.sort((a, b) => { return a - b });
        this.setWheelData();
        if (this._isFirst) {
            this._isFirst = false;
            SlotDragonGameManager.instance.initUI();
        }
    }

    reconnect(): void {
        SlotDragonGameManager.instance.reconnect();
    }

    private setWheelData() {
        for (let i = 0; i < this.wheel.length; i++) {
            this.wheelData[i] = this.wheel[i].lineWheel;
        }
    }
}


