import { Widget } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { UIDragonResultItem } from './UIDragonResultItem';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { UIDGMiniGame } from './UIDGMiniGame';
import { SlotDragonGameManager } from '../SlotDragonGameManager';
import { SlotDragonAnimation } from '../SlotDragonAnimation';
import { sp } from 'cc';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('UIDragonResult')
export class UIDragonResult extends Component {
    @property(UIDragonResultItem)
    bitWin: UIDragonResultItem = null;
    @property(UIDragonResultItem)
    mageWin: UIDragonResultItem = null;
    @property(UIDragonResultItem)
    superWin: UIDragonResultItem = null;
    @property(UIDragonResultItem)
    totalWin: UIDragonResultItem = null;
    @property(Node)
    btnClose: Node = null;
    @property(Node)
    btnFreeClose: Node = null;

    private static _instance: UIDragonResult = null;
    public static get instance() {
        return this._instance;
    }

    private _winScore: number = 0;
    private _level = 20;
    private _betScore = 0;
    private _isFirst = true;

    public isNext = false;
    public isBest = false;
    private _callback: Function = null;
    private _mult = 0;

    protected onLoad() {
        if (UIDragonResult._instance) {
            this.node.destroy();
            return;
        }

        UIDragonResult._instance = this;
        const widget = this.node.getComponent(Widget);
        widget.left = 0;
        widget.right = 0;
        this.node.position = Vec3.ZERO;
        this.node.active = false;
    }

    protected onDestroy(): void {
        UIDragonResult._instance = null;
    }

    public showResult(winScore: number, betScore: number, callback: Function) {
        this._mult = winScore / betScore;
        this._callback = callback;
        this._level = 20;
        this.isNext = false;
        this.isBest = false;
        this._winScore = winScore ? winScore : 0;
        this._betScore = betScore;
        if (!this._mult || this._mult < 10) return this._callback && (this._callback(), this._callback = null);
        SlotDragonGameManager.instance.stopAllAudio();
        SlotDragonGameManager.instance.isDialog = true;
        AudioMgr.inst.play("audio/AC_sound_8004_music_interface_003", 1, ModuleDef.SLOTS_LOONG);
        this.node.on(Node.EventType.TOUCH_END, this.onBtnNext, this);
        this.node.active = true;
        if (this._mult >= 10) {
            this.bitWin.show(betScore, winScore, 0);
        }
    }

    /**
     * 展示小游戏的结算页
     * @param winScore 
     */
    public showMiniResult(winScore: number) {
        SlotDragonGameManager.instance.stopAllAudio();
        SlotDragonGameManager.instance.isDialog = true;
        AudioMgr.inst.play("audio/AC_sound_8004_music_interface_003", 1, ModuleDef.SLOTS_LOONG);
        this.node.active = true;
        if (!SlotDragonGameManager.instance.isAuto)
            this.node.on(Node.EventType.TOUCH_END, this.onBtnResult, this);
        this._winScore = winScore;
        this.totalWin.showMini(winScore);
    }

    /**
     * 展示免费游戏的结算页
     * @param winScore 
     * @param callback 
     */
    public showFreeResult(winScore: number, callback: Function) {
        SlotDragonGameManager.instance.stopAllAudio();
        SlotDragonGameManager.instance.isDialog = true;
        AudioMgr.inst.play("audio/AC_sound_8004_music_interface_003", 1, ModuleDef.SLOTS_LOONG);
        this.node.active = true;
        this._callback = callback;
        if (!SlotDragonGameManager.instance.isAuto)
            this.node.on(Node.EventType.TOUCH_END, this.onBtnFreeResult, this);
        this._winScore = winScore;
        this.totalWin.showFree(winScore);
    }

    public showNext(level: number, betScore: number, winScore: number) {
        this.isNext = false;
        if (level === 20) {
            this._level = 100;
            this.bitWin.hide();
            this.mageWin.show2(betScore, winScore, 20 * betScore);
        } else if (level === 100) {
            this._level = -1;
            this.mageWin.hide();
            this.superWin.show2(betScore, winScore, 100 * betScore);
        }
    }

    public onBtnResult() {
        this.offListener();
        this.totalWin.setWinMiniScore();
        this.btnClose.active = true;
        this.btnFreeClose.active = false;
    }

    public onBtnFreeResult() {
        this.offListener();
        this.totalWin.setWinMiniScore();
        this.btnClose.active = false;
        this.btnFreeClose.active = true;
    }

    public onBtnNext() {
        if (this.isBest) {
            this.onBtnClose();
            return;
        }
        this.bitWin.unAllCallbacks();
        this.mageWin.unAllCallbacks();
        this.superWin.unAllCallbacks();
        this.bitWin.hide();
        this.mageWin.hide();
        this.superWin.hide();

        if (this._mult >= 10 && this._mult <= 20) {
            this.bitWin.setWinScore(this._winScore);
        } else if (this._mult > 20 && this._mult <= 100) {
            this.mageWin.setWinScore(this._winScore);
        } else {
            this.superWin.setWinScore(this._winScore);
        }

        this.isBest = true;
        this.scheduleOnce(() => {
            this.onBtnClose();
        }, 2)
    }

    private toNext() {
        this.showNext(this._level, this._betScore, this._winScore);
    }

    protected onDisable(): void {
        this.hide();
    }

    public hide() {
        this.node.off(Node.EventType.TOUCH_END, this.onBtnNext, this);
        this.offListener();
        this.unscheduleAllCallbacks();
        this.onBtnClose();
        this.isNext = false;
        this.isBest = false;
        this.bitWin.hide();
        this.mageWin.hide();
        this.superWin.hide();
        this.totalWin.hide();
        this.btnClose.active = false;
        this.btnFreeClose.active = false;
        this._callback && (this._callback(), this._callback = null);
        SlotDragonGameManager.instance.isDialog = false;
        if (this._isFirst) {
            this._isFirst = false;
            return;
        }
        AudioMgr.inst.play("audio/AC_sound_8004_music_interface_001", 1, ModuleDef.SLOTS_LOONG);
    }

    public offListener() {
        this.node.off(Node.EventType.TOUCH_END, this.onBtnResult, this);
        this.node.off(Node.EventType.TOUCH_END, this.onBtnFreeResult, this);
    }

    public onBtnClose() {
        this.node.active = false;
    }

    public onBtnBack() {
        SlotDragonAnimation.instance.isShow();
        UIDGMiniGame.instance.hide();
        SlotDragonGameManager.instance.gameOver();
        this.node.active = false;
    }
}


