import { assetManager } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { GameMgr } from 'db://assets/module_basic/scripts/GameMgr';
import { RoomMgr } from 'db://assets/module_basic/scripts/RoomMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('SlotLoongGameMgr')
export class SlotLoongGameMgr extends GameMgr {
    private static _inst: SlotLoongGameMgr;
    public static get inst(): SlotLoongGameMgr {
        if (!this._inst) {
            this._inst = new SlotLoongGameMgr();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.SLOTS_LOONG;
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC };
        this._gameScene = { name: 'game_slots_loong', bundle: ModuleDef.SLOTS_LOONG };
        this.initNetMsgHandlers();
    }
    initNetMsgHandlers() {

    }

    initSoloMode() {
        RoomMgr.inst.reset();
        this.reset();
        SubGameMgr.gameMgr = this;
    }

    private _porcessFun: Function;
    public override async preLoadRes(porcessFun: Function): Promise<boolean> {
        this._porcessFun = porcessFun;
        if (porcessFun) porcessFun(0);
        await this.preLoadMain();
        await this.preLoadLanguage();
        if (porcessFun) porcessFun(1);
        return true;
    }

    /**预加载主场景 */
    public preLoadMain(): Promise<void> {
        return new Promise((resolve, reject) => {

            let bundle = assetManager.getBundle(this._gameScene.bundle);
            if (bundle) {
                bundle.preloadScene(this._gameScene.name, (finished: number, total: number) => {
                    this._porcessFun && this._porcessFun(finished / total);
                }, () => {
                    this._porcessFun(1);
                    resolve();
                });
            }
            else {
                resolve();
            }
        })
    }

    /**加载语言包 */
    public preLoadLanguage(): Promise<void> {
        return new Promise((resolve, reject) => {
            resolve();
        })
    }
}

SubGameMgr.inst.registerGameMgr(SlotLoongGameMgr.inst);
